.grafica-dia-botones{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.grafica-dia-botones div{
    width: 33%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.grafica-dia-boton{
    width: 100%;
    padding: 5px 20px;
    margin: 10px;
    border: solid 2px rgb(255, 255, 255);
    background-color: transparent;
    color: #fff;
    border-radius: 5px;
    font-size: 17px;
    font-weight: 400;
    text-transform: capitalize;
    cursor: pointer;
    
    transition: box-shadow 200ms;
}

.grafica-dia-boton:hover, .grafica-dia-boton:focus{
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 50px 5px rgba(0,0,0,0);
    outline: none;
}

.contenedor-boton-temperatura .activo{
    background-color: rgba(252, 162, 17, 0.6);
}

.contenedor-boton-temperatura .activo:active{
    background-color: rgba(202, 130, 14, 0.8);
  

}

.contenedor-boton-humedad .activo{
    background-color: rgba(49, 51, 185, 0.6);
}

.contenedor-boton-humedad .activo:active{
    background-color: rgba(45, 47, 163, 0.8);
    
}

.contenedor-boton-viento .activo{
    background-color: rgba(96, 209, 200, 0.6);
}

.contenedor-boton-viento .activo:active{
    background-color: rgba(96, 209, 200, 0.8);
}

@media (max-width: 580px){
    .grafica-dia-botones{
        flex-flow: column nowrap;
        align-items: flex-start;
    }

    .grafica-dia-botones div{
        width: 100%;
    }
    
    .grafica-dia-boton{
        width: 100%;
    }
}