.pronostico-dia{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column-reverse;
    margin: 20px;
    text-align: center;
    text-transform: capitalize;
    font-size: 14px;
}

.pronostico-dia p{
    margin: 6px 0px;
}


.dia-imagen{
    width: clamp(75px, 7vw, 110px);
    padding: 5px;
    height: auto;
    border-radius: 50%;
    border: 3px solid #fff;
}