#root{
    min-height: 100vh;
}

.app-clima{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: wrap row;
}

.mensaje-o-contenido{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: wrap row;
    color: #fff;
}

.mensaje-resultado{
    
    width: clamp(80px, 50vw, 800px);
    padding: 20px;
    border: 3px solid #fff;
    border-radius: 10px;
    text-align: center;
    font-size: clamp(15px, 4.5vw, 25px);
    font-weight: 400;
}

