.ciudad{
    width: 200px;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    cursor: pointer;
    border: 3px solid #fff;
    color: #ffffff;

    animation-duration: 500ms;
    animation-name: aparicionCiudades;

    transition: box-shadow 200ms;
}

.ciudad:focus{
    outline: none;
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 50px 5px rgba(0,0,0,0);
}

.ciudad:hover{

    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 50px 5px rgba(0,0,0,0);
}

.ciudad ul{
    padding-left: 20px;
    margin-bottom: 10px;
}

@keyframes aparicionCiudades{
    from{
        margin: 100px 0px;
    }
    to{
        margin: 10px;
    }
}
