
.contenedor-de-cajas{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 3px solid #fff;
    border-radius: 10px;
    width: clamp(150px, 60vw, 1000px);
    height: 200px;

}

.cajas {
    padding: 30px;
    width: 150px;
    height: 150px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.cajas .caja {
    margin: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: linear-gradient(130deg, hsl(230, 60%, 74%) 0%, hsl(304, 52%, 75%) 100%);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    -webkit-animation: pulse 1s linear infinite alternate;
    animation: pulse 1s linear infinite alternate;
}

.cajas .caja:nth-child(1) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.cajas .caja:nth-child(2) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.cajas .caja:nth-child(3) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.cajas .caja:nth-child(4) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.cajas .caja:nth-child(5) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

.cajas .caja:nth-child(6) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

.cajas .caja:nth-child(7) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
}

.cajas .caja:nth-child(8) {
    -webkit-animation-delay: 0.7s;
    animation-delay: 0.7s;
}

.cajas .caja:nth-child(9) {
    -webkit-animation-delay: 0.8s;
    animation-delay: 0.8s;
}

@-webkit-keyframes pulse {
    from, 40% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    80%, to {
        opacity: 1;
    }
}

@keyframes pulse {
    from, 40% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    80%, to {
        opacity: 1;
    }
}

@media screen and (max-width: 300px) {
    .caja {
        width: 30px;
        height: 30px;
    }
}