.input-ciudad{
    display: block;
    height: 35px;
    width: clamp(100px, 35vw, 400px);
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 3px solid rgba(255, 255, 255, 0.37);
    background: transparent;
    color: #fff;
    font-size: clamp(15px, 3vw, 20px);

    transition: border-bottom 300ms;
}  

.input-ciudad::placeholder{
    color: rgba(255, 255, 255, 0.651);
}  

.input-ciudad::selection{
    background: #4159d093;
}  

.input-ciudad:focus{
    border-bottom: 3px solid #fff;
    outline: none;
} 
