.unidad-temperatura{
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  
  .unidad-temperatura button{
    margin: 0px 5px;
    border: none;
    font-size: 24px;
    cursor: pointer;
    background-color: transparent;
    color: rgba(255, 255, 255, 0.322);
    font-weight: 400;
  }


  .unidad-temperatura .activo{
    color: rgb(255, 255, 255);
  }
  
  
  .unidad-temperatura span{
    font-size: 24px;
  }