.boton-busqueda{
    width: 120px;
    height: 45px;
    padding: 3px 20px 5px 20px;
    margin: 0px 20px;
    border-radius: 5px;
    border: 3px solid;
    color: #fff;
    background-color: transparent;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;

    transition: all 200ms;
}

  .boton-busqueda:hover,.boton-busqueda:focus{
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 50px 5px rgba(0,0,0,0);
    outline: none;
  }
  
.boton-busqueda:active{
  box-shadow: none;
  width: 110px;
  height: 40px;
  font-size: 17px;
}



@media (max-width: 900px){
  .boton-busqueda{
    margin: 20px 20px 0px 20px;
  }

}

