.cuerpo-datos{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto 1fr;
    width: clamp(100px, 70vw, 1200px);    
    padding: 20px;
    margin: 20px;
    border-radius: 10px;
    border: 3px solid #fff;
 
}

@media (max-width: 900px){
    .cuerpo-datos{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: column;
    }

}
